<template>
  <div class="register-main-container">
    <img src="static/imgs/背景.png" class="bg-img" />
    <form class="register-form">
      <div class="form-title">注 册</div>
      <div class="fill-box">
        <div class="id-box">
          <div>用户名</div>
          <input type="text" class="id-input" v-model="userID" />
        </div>
        <div class="pwd-box">
          <div>密码</div>
          <input type="password" class="pwd-input" v-model="userPWD" />
        </div>
        <div class="re-pwd-box">
          <div>确认密码</div>
          <input type="password" class="repwd-input" v-model="rePWD" />
        </div>
        <div class="verify-box">
          <div class="title">验证码</div>
          <input type="text" placeholder="验证码" class="verify-input" v-model="verifyCode" />
          <div class="verify-img-box">
            <img
              :src="verifyImgUrl"
              alt="验证码"
              id="verify-img"
              title="点击刷新"
              style="height:22px;margin: 2px 5px"
              @click.stop="refreshVerify"
            />
          </div>
        </div>
      </div>
      <div class="btn-box">
        <div class="register-btn" @click="handleRegisterClick">立 即 注 册</div>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'RegisterMain',
  data () {
    return {
      userID: '',
      userPWD: '',
      rePWD: '',
      verifyCode: '',
      verifyImgUrl: 'public/index.php/users/verify'
    }
  },
  methods: {
    handleRegisterClick () {
      if (this.userID) {
        if (this.userPWD) {
          if (this.rePWD) {
            if (this.verifyCode) {
              if (this.userPWD === this.rePWD) {
                axios.post('public/index.php/users/regist', {
                  'name': this.userID,
                  'password': this.userPWD,
                  'verify_code': this.verifyCode
                })
              } else {
                alert('两次密码不一致')
              }
            } else {
              alert('请输入验证码')
            }
          } else {
            alert('请填写确认密码')
          }
        } else {
          alert('请填写密码')
        }
      } else {
        alert('请填写用户名')
      }
    },
    refreshVerify () {
      this.verifyImgUrl = 'static/imgs/加载.png'
      setTimeout(() => {
        this.verifyImgUrl = 'public/index.php/users/verify'
      }, 250)
    }
  }
}
</script>

<style scoped lang="stylus">
.register-main-container
  width 100%
  height 100%
  display flex
  align-items center
  position relative

  .bg-img
    width 100%

  .register-form
    min-width 350px
    position absolute
    top 35%
    left 70%
    width 20%
    background #FFFFFF
    border #d7d7d7 2px solid
    border-radius 0.7rem
    padding 0.7rem 1.5rem

    .form-title
      padding 0.3rem
      font-size 0.8rem
      text-align center
      font-weight 600

    .fill-box
      display flex
      flex-direction column
      width 100%

      .id-box, .pwd-box, .re-pwd-box
        display flex
        margin-top 0.6rem

        div
          width 25%
          font-size 0.65rem
          display flex
          align-items center

        .id-input, .pwd-input, .repwd-input
          background #eef8ff
          width 75%
          padding 0.2rem 0.4rem
          border-radius 0.3rem
          color rgb(100, 100, 100)
          border #d7d7d7 1px solid

      .verify-box
        width 100%
        display flex
        // grid-template-columns 25% 40% 35%
        margin-top 0.6rem

        .title
          width 25%
          font-size 0.65rem
          display flex
          align-items center

        .verify-input
          width 40%
          background #eef8ff
          padding 0.2rem 0.4rem
          border-radius 0.3rem
          color rgb(100, 100, 100)
          border #d7d7d7 1px solid

          .verify-img-box
            width 35%
            display inline-block

    .btn-box
      width 100%
      margin-top 0.7rem
      display flex
      flex-direction column
      align-items center

      .register-btn
        padding 0.3rem 0.6rem
        font-size 0.55rem
        color white
        background #1a6da8
        border #d7d7d7 1px solid
        margin 0.5rem 0
</style>



